.list {
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    grid-gap: 16px;
    margin-top: 20px;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.movieLink {
    transition: scale 250ms linear, font-weight 250ms linear;
}

.movieLink:hover {
    font-weight: 600;
    scale: 1.05;
}

.movieImg {
    display: block;
    width: 100%;
    object-fit: cover;
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.movieImg:hover {
    transform: scale(1.05);
}

.movieTitle {
    margin: 5px 0 0 0;
    text-align: center;
}